//Navbar.js
import React,{useEffect, useState} from 'react';
import SingleItem from './SingleItem';
import './items.scss';

import Strings from '../../utils/Localization'
const Items=({ items}) => {
  let itemKey = 0;
  return (
    items && (<div className='col-12 col row'>
            {items.map(
                i => {
                    return <SingleItem {...i} key={'item'+itemKey++}/>
                }
            )}
          </div>)
  )
};
export default Items;