//Navbar.js
import React,{useEffect, useState} from 'react';
import Strings from '../../utils/Localization'

const Items=({ title, image, link, price, source}) => {
  function goTo(link) {
    window.open(link)
  }
  return (
    <div className='col col-12 col-md-10 col-lg-4 ' onClick={()=>{goTo(link)}}>
      <div className='col-inner single-card'>
      <div className='img-container'>
        <img src={image} alt={title}/>
      </div>        
      <a href={link} target='_blank'  rel="noreferrer">{title}</a>
      <h5>{price}</h5>
      <h6>{source}</h6>

    </div>          
    </div>          
  )
};
export default Items;