import React, { Component } from 'react';
import Loading from '../common/loading/Loading'
import Navbar from '../common/navBar/NavBar';
import Strings from '../utils/Localization'
import Items from '../components/items/Items'
import Filter from '../components/filter/Filter'
import FilterMobile from '../components/filter/FilterMobile'
import MediaQuery from 'react-responsive'
import prepareEndpoint from '../utils/endpoints';
import {getData} from '../utils/API';
import {toastrMessage} from '../utils/functions';
import './main.scss'
class SearchPage extends Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: false,
            pricesRange: {
                low: 200,
                mid: 350,
                max: 500
            },
            count: 0,
            search: '',
            data: [],
            searchData: []
        }
    }
    searchProduct = async (search) => {
        this.setState({loading: true});
        const url = `${prepareEndpoint('Products', 'All')}?query=${search}`;
        let resp = await getData(url);
        let data = []
        if(resp.status === 200){
            data = resp.data;
        }else{
            toastrMessage('Ops! Algo salio mal, por favor intenta de nuevo','error')
        }
        this.setState({search, count: data.length, data, searchData: data, loading: false});
    }
    orderBy = type => {
        const data = [...this.state.searchData];
        let val = [-1, 1];
        if(type === 'lowest'){
            val = [1, -1];
        }
        data.sort((a, b) => (parseInt(a.price) > parseInt(b.price)) ? val[0] : val[1])
        this.setState({searchData: data});
    }
    setRange = (min,max) => {
        min = min || 0 ;
        const data = [...this.state.data].filter(item => item.price >= min && item.price <=max);
        this.setState({searchData: data});
    }
    reset = () => {
        const searchData = this.state.data
        this.setState({searchData})
    }
    render() {
        return (
            <>
                <Navbar searchProduct={this.searchProduct} />
                <h1>{Strings.title}</h1>      
                {this.state.loading && <Loading  />}
                <div className='count-bar col-12'>
                    {this.state.count !==0 && <span>{Strings.subTitle.replace('{0}', this.state.count).replace('{1}', this.state.search)}</span>}
                </div>
                {this.state.data.length > 0 &&
                (<>
                    <MediaQuery maxDeviceWidth={767}>
                        <div className='margin-10'>
                            {this.state.count !==0 && <FilterMobile  orderBy={this.orderBy} setRange={this.setRange} />}
                        </div>
                    </MediaQuery>
                    <div className='row result-container justify-content-center'>
                        <MediaQuery minDeviceWidth={767}>
                            <div className='col col-4 left'>
                                <Filter {...this.state.pricesRange} 
                                    orderBy={this.orderBy} 
                                    setRange={this.setRange} 
                                />
                            </div>
                        </MediaQuery>
                        <div className='col col-12 col-md-8 right'>
                            <Items items={this.state.searchData}/>
                        </div>
                    </div>
                </>)
                }
            </>
           );
    }
}
export default SearchPage