//Navbar.js
import React,{useEffect, useState} from 'react';
import './filter.scss'
import Strings from '../../utils/Localization'
const Filter=({ low,max, mid, orderBy, setRange }) => {    
    const [customRanges, setCustomRanges] = useState({
        max: '',
        min:''
    });
    const [rangeSelected, setRangeSelected] = useState(false);
    const [orderSelected, setOrderSelected] = useState(false);

    function updateOrder (type) {
        if(orderSelected !== type){
            orderBy(type)
        }
        setOrderSelected(type);
    }
    function updateCustomRanges (min, max, pos) {
        let minVal = min
        let maxVal = max
        if(pos === rangeSelected && pos !== undefined){
            pos = false
            minVal = ''
            maxVal = ''
            min= 0
            max = 99999
        }
        setRangeSelected(pos);
        setCustomRanges({min: minVal, max: maxVal}); //save values in state
        if(max > 0){
            setRange(min, max); //update values in parent component
        }
    }
  return (
    <div className='col-12 col filter-main'>
        <div>
            <h5>
                Ordenar Por
            </h5>
            <ul>
                <li className={orderSelected === 'highest' ? 'selected' : ''} onClick={() => updateOrder('highest')}>
                    {Strings.HtoL}
                </li>
                <li className={orderSelected === 'lowest' ? 'selected' : ''} onClick={() => updateOrder('lowest')}>
                    {Strings.LtoH}
                </li>
            </ul>
        </div> 
        <div>
            <h6>
                {Strings.Price}
            </h6>
            <ul>
                <li className={rangeSelected === 0 ? 'selected' : ''} onClick={() => updateCustomRanges(0,low, 0)}>
                    lower than ${low}
                </li>
                <li  className={rangeSelected === 1 ? 'selected' : ''} onClick={() => updateCustomRanges(low, mid, 1)}>
                    ${low} - ${mid}
                </li>
                <li  className={rangeSelected === 2 ? 'selected' : ''} onClick={() => updateCustomRanges(mid,max, 2)}>
                    ${mid} - ${max}
                </li>
                <li  className={rangeSelected === 3 ? 'selected' : ''} onClick={() => updateCustomRanges(max,99999, 3)}>
                    $higher than ${max}
                </li>
            </ul>
            <div className='custom-filters'>
                <input  value={customRanges.min} onChange={e => updateCustomRanges(e.target.value, customRanges.max)} />
                <span>-</span>
                <input  value={customRanges.max} onChange={e => updateCustomRanges(customRanges.min, e.target.value)}/>
            </div>
        </div>

     </div>
  )
};
export default Filter;